.loader {
  width: 100px;
  height: 100px;
  align-items: center;
  justify-items: center;
  display: grid;
  position: relative;
}

.loader:before, .loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .3);
}

.loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #ff3d00;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

.container {
  margin-bottom: 20px;
}

.cat-info {
  display: flex;
}

.cat-text {
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px;
  display: flex;
}

.cat-text h2 {
  margin: 0 0 10px;
  font-size: 24px;
}

.cat-text p {
  margin: 0 0 10px;
  font-size: 16px;
}

